
import { OnInit, Component, OnDestroy, AfterViewInit, EventEmitter, Input, Inject, Output } from '@angular/core';
import { FirebaseApp } from 'angularfire2';
import 'tinymce/plugins/print/plugin.js';
import 'tinymce/plugins/textcolor/plugin.js';
import 'tinymce/plugins/colorpicker/plugin.js';
import 'tinymce/plugins/preview/plugin.js';

@Component({
  selector: 'simple-tiny',
  template: `
  <script src="https://www.gstatic.com/firebasejs/4.1.3/firebase.js"></script>
  <textarea id="{{elementId}}"></textarea>
  <input id="file-input" type="file" name="name" style="display: none;" />
  `
})
export class TinyMCEComponent implements OnInit, AfterViewInit, OnDestroy {
  @Input() elementId: String;
  @Output() onEditorKeyup = new EventEmitter<any>();
  storageRef: any
  targetRef: any;
  @Output() downloadLink: string;
  constructor( @Inject(FirebaseApp) firebaseApp: any) {
    this.storageRef = firebaseApp.storage().ref();
    // this.storageRef.getDownloadURL().then(url => {
    //   console.log("download URL : ", url);
    // });
  }

  editor;

  ngAfterViewInit() {
    tinymce.init({
      selector: '#' + this.elementId,
      plugins: ['link', 'paste', 'table', 'print','preview','colorpicker','textcolor'],
      // toolbar: 'mybutton',
      toolbar1: 'styleselect | alignleft aligncenter alignright alignjustify | forecolor backcolor | bold italic underline | undo redo |',
      toolbar2: 'uplaodButton | preview',
      skin_url: 'assets/skins/lightgray',
      menubar: false,
      image_advtab: true,
      templates: [
        { title: 'Test template 1', content: 'Test 1' },
        { title: 'Test template 2', content: 'Test 2' }
      ],
      content_css: [
        '//fonts.googleapis.com/css?family=Lato:300,300i,400,400i',
        '//www.tinymce.com/css/codepen.min.css'
      ],
      setup: editor => {
        this.editor = editor;
        editor.on('keyup', () => {
          const content = editor.getContent();
          this.onEditorKeyup.emit(content);
        });
        editor.addButton('uplaodButton', {
          text: 'upload',
          icon: false,
          onclick: () => {
            this.initUpload();
          }

        });
      },
    });
  }

  initUpload() {
    console.log("test");
    var input = document.createElement('input');
    input.onchange = ($event) => {
      this.onChange($event)
    }
    input.type = 'file';
    input.click();
  }
rve
  onChange(fileInput: any) {
    // let targetFile = fileInput.srcElement.files[0];
    let file = fileInput.srcElement.files[0];
    let fileName = file.name;
    console.log("fileName : ", fileName);

    let fbsPath = 'images/' + fileName;
    this.uploadFile(fbsPath, file);
  }

  uploadFile(fbsPath: string, targetFile: any) {
    console.log("on upload file");
    console.log("fbsPath : ", fbsPath);
    console.log("targetFile : ", targetFile);
    let promise = new Promise((res, rej) => {
      this.targetRef = this.storageRef.child(fbsPath);
      let task = this.targetRef.put(targetFile);
      task.on('state_changed',
        (snapshot: any) => {
          console.log(snapshot.state);
        },
        (error: any) => {
          console.log(error.code);
          // if (true) {
          //   let msg = "Error " + error.code;
          //   window.alert(msg);
          // }
          rej(error);
        },
        () => {
          let downloadUrl = task.snapshot.downloadURL;
          console.log(downloadUrl);
          this.downloadLink = downloadUrl;
          // this.editor.insertContent('&nbsp;<image src="{{downloadUrl}}">aaa</image>&nbsp;');
          this.editor.execCommand('mceInsertContent', false, '<img width="100" src="' + downloadUrl + '"/>');
          // if (true) {
          //   let msg = "Uploaded ---> " + fbsPath;
          //   window.alert(msg);
          // }
          res(downloadUrl);
        }
      );
    })
    return promise;
  }

  ngOnDestroy() {
    tinymce.remove(this.editor);
  }

  // constructor() { }

  ngOnInit() {
  }

}



