import { Component, OnInit } from '@angular/core';

@Component({
  selector: 'app-quiz-info',
  templateUrl: './quiz-info.component.html',
  styleUrls: ['./quiz-info.component.scss']
})
export class QuizInfoComponent implements OnInit {
  learningGroupList = ['ภาษาไทย','ภาษาอังกฤษ','คณิตศาสตร์','สังคมศึกษาฯ','วิทยาศาสตร์','สุขศึกษาและพละศึกษา','ศิลปะ','การงานอาชีพและเทคโนโลยี'];
  selectedLearningGroup = this.learningGroupList[0];

  classLevelList = ['ประถมศึกษาตอนต้น','ประถมศึกษาตอนปลาย'];
  selectedClassLevel = this.classLevelList[0];

  quizTypeList = ['ปรนัย','ถูกผิด'];
  selecedQuizType = this.quizTypeList[0];

  difficultyList = ['ง่าย','ปานกลาง','ยาก'];
  selectedDifficulty = this.difficultyList[0];

  numberofChoiceList = ['2','3','4']
  selectedNumberofChoice = this.numberofChoiceList[0];
  

  constructor() { }

  ngOnInit() {
  }

  keyupHandlerFunction(event:any){
    console.log(event);
  }

}



