import { Component, OnInit } from '@angular/core';
import { Http } from "@angular/http";


declare const filestack: {
  init(apiKey: string): {
    pick({ maxFiles }: { maxFiles: number }):
      Promise<{ filesUploaded: { url: string }[] }>
  }
};

@Component({
  selector: 'app-service',
  templateUrl: './service.component.html',
  styleUrls: ['./service.component.scss']
})
export class ServiceComponent
  implements OnInit {

  uploadedFileUrls: string[] = [];

  async showPicker() {
    const client = filestack.init('AYpZIrumSNq7nIcoIQfpwz');
    const result = await client.pick({ maxFiles: 1 });
    console.log("result : ",result);
    const url = result.filesUploaded[0].url;
    console.log("url : ",url);
    this.uploadedFileUrls.push(url);
  }

  baseURL = 'http://question-answer-system-api.herokuapp.com'; // don't end with "/"
  cbase(url, arr) { //create get URL with multi param
    var entry, i, len;
    url = this.baseURL + '/' + url;
    for (i = 0, len = arr.length; i < len; i++) {
      entry = arr[i];
      url += '/' + entry;
    }
    return url;
  }

  private get(url, params) {
    return this.http.get(this.cbase(url, params)).map(res => res.json());
  }

  private post(url, params) {
    return this.http.post(this.baseURL + '/' + url, params).map(res => res.json());
  }

  listAccount() {
    return this.get('user/listUser', []);
  }

  createAccount(data) {
    return this.post('user/addUser', data);
  }




  constructor(public http: Http) { }

  upload() {
    this.showPicker();
  }
  ngOnInit() {



    this.listAccount().subscribe(
      res => {
        console.log(res);
      },
      error => {
        console.log(error);
      });



    let accountData = {
      name: "Kanrawee Karaboot",
      accountType: "Student",
      username: "tulakan_test",
      password: "1234567",
    }
    this.createAccount(accountData).subscribe(
      res => {
        console.log(res);
      },
      error => {
        console.log(error);
      })
  }

}



