import { RouterModule, Routes } from '@angular/router';

import { AppMainComponent } from './main.component';
// import { DashboardComponent } from '../dashboard/dashboard.component';

import { ChartsRoutes } from '../charts/charts-routing.module';
import { ECommerceRoutes } from '../ecommerce/ecommerce-routing.module';
import { FormsRoutes } from '../forms/forms-routing.module';
import { PagesRoutes } from '../pages/pages-routing.module';
import { PageLayoutsRoutes } from '../page-layouts/page-layouts-routing.module';
import { TablesRoutes } from '../tables/tables-routing.module';
import { UIRoutes } from '../ui/ui-routing.module';
import { ServiceComponent } from "app/service/service.component";
import { TestPageComponent } from "app/test-page/test-page.component";
import { AccountComponent } from "app/custom/account/account.component";
import { AccountInfoComponent } from "app/custom/account-info/account-info.component";
import { DashboardComponent } from "app/custom/dashboard/dashboard.component";
import { PlaylistComponent } from "app/custom/playlist/playlist.component";
import { PlaylistInfoComponent } from "app/custom/playlist-info/playlist-info.component";
import { QuizInfoComponent } from "app/custom/quiz-info/quiz-info.component";
import { QuizbankComponent } from "app/custom/quizbank/quizbank.component";
import { ReportStudentComponent } from "app/custom/report-student/report-student.component";
import { ReportPlaylistComponent } from "app/custom/report-playlist/report-playlist.component";
import { ReportComponent } from "app/custom/report/report.component";

const routes: Routes = [
    {
        path: 'app',
        component: AppMainComponent,
        children: [
            { path: '', redirectTo: '/app/dashboard', pathMatch: 'full' },
            // { path: 'dashboard', component: DashboardComponent },
            { path: 'service', component: ServiceComponent },
            { path: 'testpage', component: TestPageComponent },
            //custom path
            { path: 'account', component: AccountComponent },
            { path: 'account/accountInfo', component: AccountInfoComponent },
            { path: 'dashboard', component: DashboardComponent },
            { path: 'playlist', component: PlaylistComponent },
            { path: 'playlist/playlistInfo', component: PlaylistInfoComponent },
            { path: 'quizbank', component: QuizbankComponent },
            { path: 'quizbank/quizInfo', component: QuizInfoComponent },
            { path: 'report', component: ReportComponent },
            { path: 'report/reportPlaylist', component: ReportPlaylistComponent },
            { path: 'report/reportStudent', component: ReportStudentComponent },
            ...ChartsRoutes,
            ...ECommerceRoutes,
            ...FormsRoutes,
            ...PagesRoutes,
            ...PageLayoutsRoutes,
            ...TablesRoutes,
            ...UIRoutes,
        ]
    }
];

export const MainRoutingModule = RouterModule.forChild(routes);




