import { Component, OnInit } from '@angular/core';

declare const filestack: {
  init(apiKey: string): {
    pick({ maxFiles }: { maxFiles: number }):
      Promise<{ filesUploaded: { url: string }[] }> 
  }
};

@Component({
  selector: 'app-upload-file-manager',
  templateUrl: './upload-file-manager.component.html',
  styleUrls: ['./upload-file-manager.component.scss']
})
export class UploadFileManagerComponent implements OnInit {

  constructor() { }

  ngOnInit() {
  }

  uploadedFileUrls: string[] = [];

  async showPicker() {
    const client = filestack.init('myApiKey');
    const result = await client.pick({ maxFiles: 1 });
    const url = result.filesUploaded[0].url;
    this.uploadedFileUrls.push(url);
  }

}



